import React from "react";
import { Button, Card, CardBody, CardTitle, Jumbotron, Row } from "reactstrap";
import IntlMessages from "../../helpers/IntlMessages";
import { Colxx, Separator } from "../../components/common/CustomBootstrap";
import Breadcrumb from "../../containers/navs/Breadcrumb";
import RadialProgressCard from "../../components/cards/RadialProgressCard";

const Dashboard = ({ match }) => (
  <>
    <Row>
      <Colxx xxs="12">
        <Breadcrumb heading="menu.dashboard" match={match} />
        <Separator className="mb-5" />
      </Colxx>
    </Row>
    <Row>
      <Colxx xxs="12" md="9" className="mb-4">
        <Card>
          <CardBody>
            <Jumbotron>
              <Row className="icon-cards-row mb-2">
                <Colxx xxs="6" sm="4" md="4" lg="4">
                  <Card className="mb-4" color="primary">
                    <CardTitle className="p-4">
                      <p className="lead text-center text-white">
                        <IntlMessages id="user.dashboard.card-1.title" />
                      </p>
                    </CardTitle>
                    <CardBody className="text-center">
                      <p className="text-center">
                        <IntlMessages id="user.dashboard.card-1.desc" />
                      </p>

                      <Button>
                        <IntlMessages id="user.dashboard.card-1.button" />
                      </Button>
                    </CardBody>
                  </Card>
                </Colxx>
                <Colxx xxs="6" sm="4" md="4" lg="4">
                  <Card className="mb-4" color="primary">
                    <CardTitle className="p-4">
                      <p className="lead text-center text-white">
                        <IntlMessages id="user.dashboard.card-2.title" />
                      </p>
                    </CardTitle>
                    <CardBody className="text-center">
                      <p className="text-center">
                        <IntlMessages id="user.dashboard.card-1.desc" />
                      </p>

                      <Button>
                        <IntlMessages id="user.dashboard.card-2.button" />
                      </Button>
                    </CardBody>
                  </Card>
                </Colxx>
                <Colxx xxs="6" sm="4" md="4" lg="4">
                  <Card className="mb-4" color="primary">
                    <CardTitle className="p-4">
                      <p className="lead text-center text-white">
                        <IntlMessages id="user.dashboard.card-2.title" />
                      </p>
                    </CardTitle>
                    <CardBody className="text-center">
                      <p className="text-center">
                        <IntlMessages id="user.dashboard.card-1.desc" />
                      </p>

                      <Button>
                        <IntlMessages id="user.dashboard.card-2.button" />
                      </Button>
                    </CardBody>
                  </Card>
                </Colxx>
              </Row>
            </Jumbotron>
          </CardBody>
        </Card>
      </Colxx>

      <Colxx xxs="12" md="3" className="mb-4">
        <Row className="mb-4">
          <Colxx>
            <Card>
              <CardBody className="text-center">
                <i className="iconsminds-user lead" />
                <p className="card-text font-weight-semibold mb-4">
                  <IntlMessages id="Current Hackathon" />
                </p>
                <Button color="primary outlined">Participated</Button>
              </CardBody>
            </Card>
          </Colxx>
        </Row>
        <Row className="mb-4">
          <Colxx>
            <RadialProgressCard title="Hack for Cause" percent={80} />
          </Colxx>
        </Row>
        <Row className="mb-4">
          <Colxx>
            <RadialProgressCard title="Hack for Cause" percent={20} />
          </Colxx>
        </Row>
      </Colxx>
    </Row>
  </>
);
export default Dashboard;

